// _mixins.scss

// Media query mixin for tablets
@mixin tablet {
  @media (max-width: 768px) {
    @content;
  }
}

// Media query mixin for mobile phones
@mixin mobile {
  @media (max-width: 576px) {
    @content;
  }
}
@mixin desktop {
  @media (min-width: 1024px) {
    @content;
  }
}